@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

body {
	height: 100%;
	min-width: 320px;
	position: relative; }

.wrapper {
	margin: 0 auto;
	min-height: 100vh;
	overflow: hidden;
	position: relative;
	padding-top: $header-height;

	.home-page & {
		padding-top: 0;

		@include media('<desk-sm') {
			padding-top: $header-height--lg; } }

	@include media('<desk-lg') {
		padding-top: $header-height--lg; }

	&::before, &::after {
		@include pseudo();
		top: 0;
		left: $page-layout-width;
		height: 100%;
		width: 9999px;
		background-color: rgb(250, 250, 250);
		z-index: 99998;

		@include media('<1920px') {
			left: 100%; } }

	&::after {
		border-right: 1px solid $border-color;
		width: 0; } }

.main {
	position: relative;
	width: 100%;
	float: left;
	clear: both;
	z-index: 4; }

.main__holder {
	margin: 0 0 0 #{$sidebar-width};
	min-height: 50px;
	@include clear;

	@include media('<desk-lg') {
		margin-left: $sidebar-width--lg; }

	.home-page & {
		margin: 0 #{$sidebar-width};

		@include media('<desk-lg') {
			@include margin-horizontal($sidebar-width--lg); }

		@include media('<desk-sm') {
			@include margin-horizontal(0); } } }

.content-wrap {
	float: left;
	width: 100%; }

.content {
	padding: #{floor($layout-spaces*0.9)} #{$layout-spaces};

	@include media('<desk-hd') {
		padding: #{floor($layout-spaces--hd*0.9)} #{$layout-spaces--hd}; }

	@include media('<desk-lg') {
		padding: #{floor($layout-spaces--lg*0.9)} #{$layout-spaces--lg}; }

	@include media('<desk-sm') {
		padding: #{floor($layout-spaces--sm*0.9)} #{$layout-spaces--sm}; } }

// актуально только для текущего проекта
.layout-full-width, .layout-wide {
	@include margin-horizontal(-#{$layout-spaces});

	@include media('<desk-hd') {
		@include margin-horizontal(-#{$layout-spaces--hd}); }

	@include media('<desk-lg') {
		@include margin-horizontal(-#{$layout-spaces--lg}); }

	@include media('<desk-sm') {
		@include margin-horizontal(-#{$layout-spaces--sm}); } }

.layout-top-fit {
	margin-top: -#{floor($layout-spaces*0.9)};

	@include media('<desk-hd') {
		margin-top: -#{floor($layout-spaces--hd*0.9)}; }

	@include media('<desk-lg') {
		margin-top: -#{floor($layout-spaces--lg*0.9)}; }

	@include media('<desk-sm') {
		margin-top: -#{floor($layout-spaces--sm*0.9)}; } }

.layout-bottom-fit {
	margin-bottom: -#{floor($layout-spaces*0.9)};

	@include media('<desk-hd') {
		margin-bottom: -#{floor($layout-spaces--hd*0.9)}; }

	@include media('<desk-lg') {
		margin-bottom: -#{floor($layout-spaces--lg*0.9)}; }

	@include media('<desk-sm') {
		margin-bottom: -#{floor($layout-spaces--sm*0.9)}; } }

.entry-img {
	padding: #{floor($layout-spaces*0.9)} #{$layout-spaces};

	@include media('<desk-hd') {
		padding: #{floor($layout-spaces--hd*0.9)} #{$layout-spaces--hd}; }

	@include media('<desk-lg') {
		padding: #{floor($layout-spaces--lg*0.9)} #{$layout-spaces--lg}; }

	@include media('<desk-sm') {
		padding: #{floor($layout-spaces--sm*0.9)} #{$layout-spaces--sm}; } }

/* sidebar (layout) */
.header {
	position: fixed;
	top: 0;
	z-index: 1020;
	height: $header-height;
	width: 100%;
	@if $page-layout-width {
		max-width: #{$page-layout-width}; }
	@else {
		max-width: 100%; }

	@include media('<desk-lg') {
		height: $header-height--lg; } }

.header__holder {
	@include padding-horizontal($layout-spaces);
	margin-left: $logo-width;

	@include media('<desk-hd') {
		@include padding-horizontal($layout-spaces--hd); }

	@include media('<desk-lg') {
		@include padding-horizontal($layout-spaces--lg);
		margin-left: $logo-width--lg; }

	@include media('<desk-sm') {
		@include padding-horizontal($layout-spaces--sm);
		margin-left: 0; } }

.home-page .header__holder {
	@include padding-horizontal($layout-spaces, $sidebar-width);
	margin-left: $logo-width;

	@include media('<desk-hd') {
		@include padding-horizontal($layout-spaces--hd, $sidebar-width); }

	@include media('<desk-lg') {
		@include padding-horizontal($layout-spaces--lg, $sidebar-width--lg);
		margin-left: $logo-width--lg; }

	@include media('<desk-sm') {
		@include padding-horizontal($layout-spaces--sm);
		margin-left: 0; } }

/* sidebar (layout) end */

/* sidebar (layout) */
.sidebar {
	position: fixed;
	top: $header-height;
	left: 0;
	@include size($sidebar-width, calc(100% - #{$header-height}));
	z-index: 1001;

	@include media('<desk-lg') {
		top: $header-height--lg;
		@include size($sidebar-width--lg, calc(100% - #{$header-height--lg})); }

	@include media('<desk-sm') {
		display: none; } }

.sidebar__align {
	width: calc(100% + 100px);
	background-color: inherit;

	.touchevents & {
		width: 100%; } }

.sidebar__holder {
	position: relative;
	width: $sidebar-width;

	@include media('<desk-lg') {
		width: $sidebar-width--lg; } }

/* sidebar (layout) end */

/* footer (layout) */
.footer {
	position: relative;
	clear: both;
	z-index: 5; }

.footer__holder {
	$base-width: 100% - #{$sidebar-width};
	$base-width--lg: 100% - #{$sidebar-width--lg};

	& {
		left: $sidebar-width;
		width: calc(#{$base-width});
		@include padding-horizontal($layout-spaces);

		@include media('<desk-hd') {
			@include padding-horizontal($layout-spaces--hd); }

		@include media('<desk-lg') {
			@include padding-horizontal($layout-spaces--lg); } } }

.footer-common, .footer-bottom {
	@include padding-vertical(round($layout-spaces*0.5));

	@include media('<desk-hd') {
		@include padding-vertical(round($layout-spaces--hd*0.55)); }

	@include media('<desk-lg') {
		@include padding-vertical(round($layout-spaces--lg*0.85)); } }

/* footer (layout) end */

.max-wrap {
	@if $page-layout-width {
		max-width: $page-layout-width + $page-layout-space-horizontal*2; } }

.layout {
	//+push-auto
	@if $page-layout-space-horizontal != 0 {
		@include padding-horizontal($page-layout-space-horizontal); }

	//+media('<tab-lg')
