// элеметы с отступами по бокам
.layout-wide {
	//+padding-horizontal($text-layout-space-horizontal)
	@include padding-horizontal($layout-spaces);

	@include media('<desk-hd') {
		@include padding-horizontal($layout-spaces--hd); }

	@include media('<desk-lg') {
		@include padding-horizontal($layout-spaces--lg); }

	//+media('<tab-lg')
	//include padding-horizontal($text-layout-space-horizontal--md)

	@include media('<mob-lg') {
		@include padding-horizontal($layout-spaces--sm); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr, blockquote {
		max-width: none; } }

// элеметы без отступов по бокам
.layout-full-width {
	p, ul, ol, h1, h2, h3, h4, h5, h6, hr, blockquote {
		max-width: none; } }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
.layout-article {
	//padding: round($text-layout-space-horizontal) #{$text-layout-space-horizontal} round($text-layout-space-horizontal*1.5)

	//+media('<tab-lg')
	//adding: round($text-layout-space-horizontal--md*1.5) #{$text-layout-space-horizontal--md} round($text-layout-space-horizontal--md*2)

	//+media('<tab-sm')
	//adding: round($text-layout-space-horizontal--sm*1.5) #{$text-layout-space-horizontal--sm} round($text-layout-space-horizontal--sm*2)

	& > p, & > ul, & > ol, & > h1, & > h2, & > h3, & > h4, & > h5, & > h6, & > hr, & > blockquote, & > .layout-text, .imgc figcaption {
		//max-width: $text-layout-width + $text-layout-indent
		//+padding-horizontal($text-layout-indent, 0)
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	& > h1, & > img, .imgc {
		display: block;
		max-width: $flood-layout-width;
		@include push-auto; }

	& > img, .imgc {
		@include margin-vertical(0, $bottom-space--lg); }

	.layout-text, .layout-flood {
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-text {
		max-width: $text-layout-width; }

	.max-wrap-text {
		max-width: $text-layout-width; }

	//& .pagination
	//ax-width: $text-layout-width
