 }	//@include margin-horizontal(-#{$text-layout-space-horizontal--sm})

/* logo (layout) */
.logo {
	width: $logo-width;
	height: $header-height;

	@include media('<desk-lg') {
		width: $logo-width--lg;
		height: $header-height--lg; }

	// home-page
	.home-page & {
		width: $logo-full-width;

		@include media('<desk-lg') {
			width: $logo-full-width--lg; } } }

// header if has large logo
.home-page {
	.header__holder {
		padding-left: $layout-spaces + $logo-full-width - $logo-width;

		@include media('<desk-hd') {
			padding-left: $layout-spaces--hd + $logo-full-width - $logo-width; }

		@include media('<desk-lg') {
			padding-left: $layout-spaces--lg + $logo-full-width--lg - $logo-width--lg; }

		@include media('<desk-sm') {
			padding-left: $layout-spaces--sm; } } }

/* logo (layout) end */

/* button toggle menu (layout) */
.btn-menu {
	@include size($sidebar-width);

	@include media('<desk-lg') {
		@include size($sidebar-width--lg); } }

/* button toggle menu (layout) end */

/* fullpage navigation / fp-nav (layout) */
#fp-nav {
	height: 100vh;
	width: $sidebar-width;
	top: 0;
	left: $page-layout-width;
	margin: 0 0 0 -#{$sidebar-width} !important;
	padding-top: $header-height;

	@include media('<1920px') {
		left: 100%; }

	@include media('<desk-lg') {
		width: $sidebar-width--lg;
		margin: 0 0 0 -#{$sidebar-width--lg} !important;
		padding-top: $header-height--lg; } }

/* fullpage navigation / fp-nav (layout) end */

.nav-bottom {
	padding-bottom: #{floor($layout-spaces*0.9)};

	@include media('<desk-hd') {
		padding-bottom: #{floor($layout-spaces--hd*0.9)}; }

	@include media('<desk-lg') {
		padding-bottom: #{floor($layout-spaces--lg*0.9)}; }

	@include media('<desk-sm') {
		padding-bottom: #{floor($layout-spaces--sm*0.9)}; }

	/* tape slider (layout) */
	.tape-slider-pagination {
		bottom: 0; }

	.tape-slider-prev, .tape-slider-next {
		bottom: 0;
		top: auto;
		margin-top: 0; }

	.tape-slider-prev {
		left: 0; }

	.tape-slider-next {
		right: 0; }
	/* tape slider (layout) end */ }

/* catalog - m-catalog (layout) */
.m-catalog-list, {
	&, .#{$user-content} .m-catalog-list {
		& > li {
			padding: nth($m-catalog__space, 1) nth($m-catalog__space, 2);

			@include media('<desk-hd') {
				padding: #{floor($layout-spaces--hd*0.9)} #{$layout-spaces--hd}; }

			@include media('<desk-lg') {
				padding: #{floor($layout-spaces--lg*0.9)} #{$layout-spaces--lg}; }

			@include media('<desk-sm') {
				padding: #{floor($layout-spaces--sm*0.9)} #{$layout-spaces--sm}; } } } }

/* catalog - m-catalog (layout) end */

/* home-page (layout) */
.main-section {
	padding-top: $header-height;

	@include media('<desk-lg') {
		padding-top: $header-height--lg; }

	@include media('<desk-sm') {
		padding-top: 0; } }

.home-page {
	/* main slider (layout) */
	.main-slider-pagination {
		left: #{$layout-spaces};
		top: #{floor($layout-spaces*0.9)};

		@include media('<desk-hd') {
			left: #{$layout-spaces--hd};
			top: #{floor($layout-spaces--hd*0.9)}; }

		@include media('<desk-lg') {
			left: #{$layout-spaces--lg};
			top: #{floor($layout-spaces--lg*0.9)}; }

		@include media('<desk-sm') {
			left: #{$layout-spaces--sm};
			top: #{floor($layout-spaces--sm*0.9)}; } }

	.main-slider-prev, .main-slider-next {
		bottom: #{floor($layout-spaces*0.9)};
		top: auto;
		margin-top: 0;

		@include media('<desk-hd') {
			bottom: #{floor($layout-spaces--hd*0.9)}; }

		@include media('<desk-lg') {
			bottom: #{floor($layout-spaces--lg*0.9)}; }

		@include media('<desk-sm') {
			bottom: #{floor($layout-spaces--sm*0.9)}; } }

	.main-slider-prev {
		left: #{$layout-spaces};

		@include media('<desk-hd') {
			left: #{$layout-spaces--hd}; }

		@include media('<desk-lg') {
			left: #{$layout-spaces--lg}; }

		@include media('<desk-sm') {
			left: #{$layout-spaces--sm}; } }

	.main-slider-next {
		right: #{$layout-spaces};

		@include media('<desk-hd') {
			right: #{$layout-spaces--hd}; }

		@include media('<desk-lg') {
			right: #{$layout-spaces--lg}; }

		@include media('<desk-sm') {
			right: #{$layout-spaces--sm}; } }

	.main-slider__item {
		padding: #{floor($layout-spaces*0.9*2)} #{$layout-spaces};

		@include media('<desk-hd') {
			padding: #{floor($layout-spaces--hd*0.9*2)} #{$layout-spaces--hd}; }

		@include media('<desk-lg') {
			padding: #{floor($layout-spaces--lg*0.9*2.5)} #{$layout-spaces--lg}; }

		@include media('<desk-sm') {
			padding: #{floor($layout-spaces--sm*0.9*2.5)} #{$layout-spaces--sm}; } }

	/* main slider (layout) end */

	.nav-bottom {
		padding-bottom: #{floor($layout-spaces*1.8)};

		@include media('<desk-hd') {
			padding-bottom: #{floor($layout-spaces--hd*1.8)}; }

		@include media('<desk-lg') {
			padding-bottom: #{floor($layout-spaces--lg*1.8)}; }

		@include media('<desk-sm') {
			padding-bottom: #{floor($layout-spaces--sm*1.8)}; }

		/* tape slider (layout) */
		.tape-slider-pagination, .tape-slider-prev, .tape-slider-next {
			bottom: #{floor($layout-spaces*0.9)};

			@include media('<desk-hd') {
				bottom: #{floor($layout-spaces--hd*0.9)}; }

			@include media('<desk-lg') {
				bottom: #{floor($layout-spaces--lg*0.9)}; }

			@include media('<desk-sm') {
				bottom: #{floor($layout-spaces--sm*0.9)}; } }

		.tape-slider-prev {
			left: #{$layout-spaces};

			@include media('<desk-hd') {
				left: #{$layout-spaces--hd}; }

			@include media('<desk-lg') {
				left: #{$layout-spaces--lg}; }

			@include media('<desk-sm') {
				left: #{$layout-spaces--sm}; } }

		.tape-slider-next {
			right: #{$layout-spaces};

			@include media('<desk-hd') {
				right: #{$layout-spaces--hd}; }

			@include media('<desk-lg') {
				right: #{$layout-spaces--lg}; }

			@include media('<desk-sm') {
				right: #{$layout-spaces--sm}; } }

		/* tape slider (layout) end */ }

	/* previews tabs (layout) */
	.previews-tabs__panel {
		//padding-top: $previews-tabs-top-space
		padding-top: $previews-tab-line-height + floor($layout-spaces*0.9*2);

		@include media('<desk-hd') {
			padding-top: $previews-tab-line-height + floor($layout-spaces--hd*0.9*2); }

		@include media('<desk-lg') {
			padding-top: $previews-tab-line-height + floor($layout-spaces--lg*0.9*2); }

		@include media('<desk-sm') {
			padding-top: $previews-tab-line-height + floor($layout-spaces--sm*0.9*2); } }

	.previews-tabs__nav {
		padding: #{floor($layout-spaces*0.9)} #{$layout-spaces};

		@include media('<desk-hd') {
			padding: #{floor($layout-spaces--hd*0.9)} #{$layout-spaces--hd}; }

		@include media('<desk-lg') {
			padding: #{floor($layout-spaces--lg*0.9)} #{$layout-spaces--lg}; }

		@include media('<desk-sm') {
			padding: #{floor($layout-spaces--sm*0.9)} #{$layout-spaces--sm}; } }

	/* previews tabs (layout) end */ }

/* feature list, images slider */
.feature-list, .images-slider {
	max-width: $flood-layout-width;
	@include push-auto;

	&:last-child {
		margin-bottom: 0; } }

.feature-list {
	margin-bottom: $bottom-space--lg;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--ml; } }

.images-slider {
	margin-bottom: $bottom-space--lg*2;

	@include media('<tab-lg') {
		margin-bottom: $bottom-space--ml*2; } }

/* feature list, images slider end */

/* subscription form */
.subscription-form {
	padding: $layout-spaces;

	@include media('<desk-hd') {
		padding: $layout-spaces--hd; }

	@include media('<desk-lg') {
		padding: $layout-spaces--lg; }

	@include media('<desk-sm') {
		padding: $layout-spaces--sm; }

	.article-footer + & {
		padding-top: 145px; } }

/* subscription form end */

/* bottom space */
.bottom-space {
	margin-bottom: $bottom-space--lg;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space--ml; }

	&:last-child {
		margin-bottom: 0; } }

/* bottom space end */

/* footer at bottom */
.spacer {
	.wrapper > & {
		display: none; }

	.home-page .main & {
		display: none; } }

/* footer at bottom end */

/* article entry (layout) */
.article-entry {
	padding-left: $aside-width; }

/* article entry (layout) end/* footer at bottom end */

/* box options (layout) */
.box-options__label {
	width: $aside-width;
	padding-right: $layout-spaces;
	margin-top: -0.4em;

	@include media('<desk-hd') {
		padding-right: $layout-spaces--hd; }

	@include media('<desk-lg') {
		padding-right: $layout-spaces--lg; }

	@include media('<desk-sm') {
		padding-right: $layout-spaces--sm; } }

.box-options__content {
	margin-left: $aside-width; }

/* box options (layout) end */

/* m-container (layout) */
.m-container {
	@include clear; }

.m-aside {
	float: left;
	width: $aside-width;
	// for sticky-sidebar js
	will-change: min-height; }

.m-aside-holder {
	// for sticky-sidebar js
	transform: translate(0, 0) /* For browsers don't support translate3d. */;
	transform: translate3d(0, 0, 0);
	will-change: position, transform;
	padding-right: $layout-spaces;

	@include media('<desk-hd') {
		padding-right: $layout-spaces--hd; }

	@include media('<desk-lg') {
		padding-right: $layout-spaces--lg; }

	@include media('<desk-sm') {
		padding-right: $layout-spaces--sm; } }

.m-content {
	margin-left: $aside-width;
	@include clear; }

.m-content__holder {
	float: left;
	width: 100%; }

/* m-container (layout) end */

/* filters tags (layout) */
.p-filters-tags.is-affixed {
	// for sticky-sidebar js
	z-index: 5; }

.p-filters-tags__holder {
	padding-left: $aside-width; }

/* filters tags (layout) end */

/* news-preview (layout) */
//.news-preview
//ax-width: $flood-layout-width

/* news-preview(layout) end */

/* user-form (layout) */
.user-form {
	&, .layout-article & {
		padding: $text-layout-space-horizontal*2 $text-layout-space-horizontal;

		@include media('<tab-lg') {
			padding: $text-layout-space-horizontal;
 }			//+padding-vertical($text-layout-space-horizontal--md)

		//+media('<mob-lg')
 } }		//include padding-vertical($text-layout-space-horizontal--sm)

/* user-form (layout) end */

/* shutter (layout) */
/* shutter (layout) end */

/* contacts (layout) */
.contacts-map__holder {
	height: calc(100vh - #{$header-height});
 }	//height: 1000px

.contacts__info {
	min-height: calc(100vh - #{$header-height});
	padding: #{floor($layout-spaces*0.9)} #{$layout-spaces};

	@include media('<desk-hd') {
		padding: #{floor($layout-spaces--hd*0.9)} #{$layout-spaces--hd}; }

	@include media('<desk-lg') {
		padding: #{floor($layout-spaces--hd*0.9)} #{$layout-spaces--lg}; }

	@include media('<desk-sm') {
		padding: #{floor($layout-spaces--sm*0.9)} #{$layout-spaces--sm}; } }

/* contacts info end */

/* contacts (layout) end */

/* product card (layout) */
.p-card__visual {
	margin-top: -#{$header-height + floor($layout-spaces*0.9)};

	@include media('<desk-hd') {
		margin-top: -#{$header-height + floor($layout-spaces--hd*0.9)}; }

	@include media('<desk-lg') {
		margin-top: -#{$header-height + floor($layout-spaces--lg*0.9)}; }

	@include media('<desk-sm') {
		margin-top: -#{$header-height + floor($layout-spaces--sm*0.9)}; } }

.p-card__gallery__item {
	padding-top: $header-height; }

/* product card (layout) end */

