@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

// social color
$facebook-color: #3a589e !default;
$vkontakte-color: #4b73a4 !default;
$twitter-color: #1da0f2 !default;
$odnoklassniki-color: #f2822e !default;
$youtube-color: #e62117 !default;
$instagram-color-start: #de4f5a !default;
$instagram-color-end: #a43b97 !default;
$social-space: 30px !default;
$social-space--lg: 20px !default;

/* social networks */
.social-networks {
	margin-bottom: $bottom-space;
	@include clear;

	&:last-child {
		margin-bottom: 0; } }

.social-networks__list {
	position: relative;
	float: left;
	width: calc(100% + #{$social-space});
	margin: 0 -#{round($social-space/2)} -#{round($social-space/2)};
	font-size: 0;

	@include media('<desk-lg') {
		width: calc(100% + #{$social-space--lg});
		margin: 0 -#{round($social-space--lg/2)} -#{round($social-space--lg/2)}; } }

.social-networks__item {
	@include display-inline-block-top;
	margin: 0 #{round($social-space/2)} round($social-space/2);
	@include font(14px, round-float(18/14, 2));
	letter-spacing: normal;

	@include media('<desk-lg') {
		margin: 0 #{round($social-space--lg/2)} round($social-space--lg/2); }

	a {
		display: block;
		position: relative;
		@include size(50px);
		border: 2px solid $text-color;
		color: $text-color;
		fill: $text-color;
		border-radius: 50%;

		@include media('<desk-sm') {
			@include size(40px); }

		span {
			@include hide-text; }

		svg {
			@include centering-transform-abs;
			@include size(20px); }

		path {
			fill: inherit; }

		.no-touchevents &:hover {
			&.fb {
				border-color: $facebook-color;
				fill: $facebook-color; }

			&.vk {
				border-color: $vkontakte-color;
				fill: $vkontakte-color; }

			&.twitter {
				border-color: $twitter-color;
				fill: $twitter-color; }

			&.ok {
				border-color: $odnoklassniki-color;
				fill: $odnoklassniki-color; }

			&.youtube {
				border-color: $youtube-color;
				fill: $youtube-color; }

			&.instagram {
				border-color: $instagram-color-end;
				fill: $instagram-color-end;
 } } } }				//+linear-gradient(to right, #{$instagram-color-start} 0%, #{$instagram-color-end} 100%)

/* social networks end */

/* share */
$social-share-space: 12px !default;
$social-share-size: 50px !default;

.social-share {
	margin-bottom: $bottom-space;
	@include clear-spacing;

	&:last-child {
		margin-bottom: 0; } }

input[type="checkbox"].social-share__check {
	@include hide-text; }

.social-share__opener {
	//&, input[type="checkbox"] ~ &
	& {
		position: relative;
		@include display-inline-block-middle;
		@include size($social-share-size);
		margin: 0 20px 0 0;
		@include font($font-size, $line-height);
		letter-spacing: normal;
		color: $text-color;
		fill: $text-color;
		text-decoration: none;
		cursor: pointer;
		border: 2px solid $text-color;
		border-radius: 50%;
		overflow: hidden;

		&::before, &::after {
			$this-icon-lines-width: 16px;
			$this-icon-lines-height: 4px;
			$this-icon-lines-color: white;
			// ==============
			@include pseudo();
			left: 50%;
			top: 50%;
			margin: -1px 0 0 -8px;
			border: none;
			@include size($this-icon-lines-width, $this-icon-lines-height);
			background-color: $this-icon-lines-color;
			display: none; }

		&::before {
			transform: rotate(-45deg); }

		&::after {
			transform: rotate(45deg); }

		&:hover {
			border-color: $link-color;
			fill: $link-color; } }

	span {
		@include hide-text; }

	svg {
		position: absolute;
		left: 50%;
		top: 50%;
		@include size(26px);
		margin: -13px 0 0 -15px; }

	path {
		fill: inherit; } }

.social-share__list {
	display: none;
	vertical-align: middle;
	position: relative;
	margin: -#{round($social-share-space/2)} 20px -#{round($social-share-space/2)} 0; }

.social-share__item {
	@include display-inline-block-top;
	margin: #{round($social-share-space/2)};
	@include font(14px, round-float(18/14, 2));
	letter-spacing: normal;

	a {
		display: block;
		position: relative;
		@include size($social-share-size);
		border: 2px solid $text-color;
		color: $text-color;
		fill: $text-color;
		border-radius: 50%;

		span {
			@include hide-text; }

		svg {
			@include centering-transform-abs;
			@include size(20px); }

		path {
			fill: inherit; }

		&:hover {
			&.fb {
				border-color: $facebook-color;
				fill: $facebook-color; }

			&.vk {
				border-color: $vkontakte-color;
				fill: $vkontakte-color; }

			&.twitter {
				border-color: $twitter-color;
				fill: $twitter-color; }

			&.ok {
				border-color: $odnoklassniki-color;
				fill: $odnoklassniki-color; }

			&.youtube {
				border-color: $youtube-color;
				fill: $youtube-color; }

			&.instagram {
				border-color: $instagram-color-end;
				fill: $instagram-color-end;
 } } } }				//+linear-gradient(to right, #{$instagram-color-start} 0%, #{$instagram-color-end} 100%)

.social-share__label {
	position: relative;
	@include display-inline-block-middle;
	@include font-d();
	color: $text-color-light;
	padding: 15px 0 15px 60px;

	&::before {
		@include pseudo();
		bottom: 50%;
		left: 0;
		@include size(40px, 0);
		border-top: 1px solid $text-color-light; } }

