.is-open {
	.social-share__list {
		display: inline-block; }

	.social-share__opener {
		margin-right: round($social-share-space/2);
		background-color: $text-color;
		user-select: none;

		&:hover {
			background-color: $link-color; }

		svg {
			display: none; }

		&::before, &::after {
			display: block; } } }

/* share end */
